.wcf__nav-menu{
  display: none;
  svg{
    width: 1em;
    height: 1em;
  }

  .wcf-submenu-indicator{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    padding-left: 5px;
  }

  .wcf-menu-badge {
    display: none;
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    position: absolute;
    right: 15px;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: var(--badge-bg);
    box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.1);
    margin-top: -22px;
    &:after{
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      border: 5px solid var(--badge-bg);
      border-bottom-color: transparent !important;
      border-inline-end-color: transparent !important;
      border-inline-end-width: 7px;
      border-inline-start-width: 0;
    }
  }

  .wcf-menu-hamburger {
    margin-left: auto;
    cursor: pointer;
    font-size: 25px;
    padding: 4px 8px;
    border: 1px solid #dee1e7;
    outline: none;
    background: transparent;
    line-height: 1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  //mobile menu active
  &.mobile-menu-active {
    display: block;

    .wcf-submenu-indicator{
      padding: 8px 10px;
      margin: -8px -10px -8px auto;
    }

    .wcf-menu-hamburger {
      display: inline-block;
    }

    .wcf-menu-close {
      align-self: flex-end;
      margin-left: auto;
      margin-top: 10px;
      margin-right: 10px;
      padding: 8px 10px;
      border: 1px solid #dee1e7;
      outline: none;
      background: transparent;
      line-height: 1;
      cursor: pointer;
      display: inline-flex;
      align-items: center;
      justify-content: center
    }

    //overlay
    .wcf-menu-overlay {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1000;
      background-color: rgba(0, 0, 0, .5);
      height: 100%;
      width: 100%;
      transition: .4s;
      opacity: 0;
      visibility: hidden;
    }

    &.wcf-nav-is-toggled {
      .wcf-nav-menu-container {
        transform: translateX(0) !important;
      }

      .wcf-menu-overlay {
        opacity: 1;
        visibility: visible;
      }
    }

    // ---------------------------------------
    // important stuff starts here
    .wcf-nav-menu-container {
      display: flex;
      flex-direction: column;
      position: fixed;
      z-index: 1001;
      top: 0;
      bottom: 0;
      width: 250px;
      background-color: #ffffff;
      overflow-y: auto;
      overflow-x: hidden;
      -webkit-overflow-scrolling: touch;
      transition: 0.45s;

      .wcf-nav-menu-nav {
        flex: 0 0 100%;
        padding: 0;
        margin: 0;
        order: 1;
      }
    }

    .menu-item {
      list-style: none;

      &:not(:last-child) a{
        border-bottom: solid 1px #DEE1E7FF;
      }

      a {
        text-decoration: none;
        display: flex;
        padding: 0.5em 1em;
        font-size: 1rem;
        line-height: 1.5em;
        transition: 0.4s;
      }
    }

    .menu-item-has-children {
      .sub-menu {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #fff;
        transform: translateX(100%);
        transition: 0.3s;
        visibility: hidden;
        padding: 0;
        margin: 0;
        flex: 0 0 100%;

        .nav-back-link {
          display: flex;
          align-items: center;
          background-color: #064AF3FF;
          color: #ffffff;
          border: none !important;
        }
      }

      &.active {
        > .sub-menu {
          transform: translateX(0);
          visibility: visible;
        }
      }
    }

    //mega menu
    .wcf-mega-menu {

      .sub-menu{
        display: none;
      }

      .wcf-mega-menu-panel {
        display: none;
        max-width: 100% !important;
        transition: 0.3s;
        opacity: 0;
        visibility: hidden;
      }

      &.active > .wcf-mega-menu-panel {
        display: block;
        opacity: 1;
        visibility: visible;
      }

      //mobile submenu
      &.mobile-wp-submenu{
        .wcf-mega-menu-panel{
          display: none !important;
        }
        .sub-menu{
          display: block;
        }
      }
    }

    &.mobile-menu-right{
      .wcf-nav-menu-container {
        transform: translateX(100%);
        right: 0;
      }
    }

    &.mobile-menu-left{
      .wcf-nav-menu-container {
        transform: translateX(-100%);
        left: 0;
      }
    }
  }

  //desktop menu active
  &.desktop-menu-active{
    display: block;
    .wcf-menu-hamburger,
    .wcf-menu-close{
      display: none;
    }

    .wcf-menu-badge {
      display: block;
    }

    .wcf-nav-menu-nav{
      display: flex;
      flex-wrap: wrap;
      margin: 0;
      padding: 0;
      &.menu-layout-vertical{
        flex-direction: column;
        .menu-item-has-children .sub-menu,
        .wcf-mega-menu .wcf-mega-menu-panel {
          left: 100%;
          top: auto;
        }
      }
    }

    .menu-item {
      list-style: none;
      position: relative;
      white-space: nowrap;
      a {
        position: relative;
        text-decoration: none;
        display: flex;
        padding: 0.5em 1em;
        transition: 0.4s;
        color: #1c1d20;
        fill: #1c1d20;
      }

      a:after{
        content: '';
        position: absolute;
        left: 0;
        transition: transform 0.25s ease-out;
        transform: scaleX(0);
        transform-origin: bottom right;
        height: 2px;
        width: 100%;
        background-color: #3f444b;
        z-index: 2;
      }
    }

    .menu-item-has-children {

      .sub-menu {
        position: absolute;
        top: 100%;
        left: 0;
        transform: translateY(-10px);
        background: #fff;
        transition: 0.3s;
        padding: 0;
        margin: 0;
        box-shadow: 2px 2px 6px rgba(0, 0, 0, .2);
        min-width: 12em;
        z-index: 99;
        opacity: 0;
        visibility: hidden;
        a {
          border-top: solid 1px #DEE1E7FF;
        }
        .sub-menu{
          top: 0;
          left: 100%;
        }
      }

      &:not(.wcf-mega-menu):hover > .sub-menu {
        transform: translateY(0px);
        opacity: 1;
        visibility: visible;
      }
    }

    //mega menu
    .wcf-mega-menu {
      &.mega-position-static{
        position: static !important;
      }

      .wcf-mega-menu-panel {
        position: absolute;
        top: 100%;
        left: 0;
        transform: translateY(-10px);
        transition: 0.3s;
        padding: 0;
        margin: 0;
        min-width: 12em;
        z-index: 99;
        opacity: 0;
        visibility: hidden;
      }

      &:hover > .wcf-mega-menu-panel {
        transform: translateY(0px);
        opacity: 1;
        visibility: visible;
      }
    }

    &.hover-pointer{

      &-dot a{
        &:after {
          width: 6px;
          height: 6px;
          border-radius: 100px;
          bottom: 0;
          left: 50%;
          transform: translateX(-50%) scale(0);
          transform-origin: center;
        }
        &:hover:after{
          transform: translateX(-50%) scale(1);
        }
      }

      &-underline a {
        &:after {
          bottom: 0;
        }
        &:hover:after{
          transform: scaleX(1);
          transform-origin: bottom left;
        }
      }

      &-overline a{
        &:after {
          top: 0;
        }
        &:hover:after{
          transform: scaleX(1);
          transform-origin: bottom left;
        }
      }

      &-line-through a{
        &:after {
          top: 50%;
          transform: translateY(-50%) scaleX(0);
        }
        &:hover:after{
          transform: translateY(-50%) scaleX(1);
          transform-origin: bottom left;
        }
      }

      &-flip a {
        .menu-text{
          position: relative;
          transition: transform 0.3s;
          transform-origin: 50% 0;
          transform-style: preserve-3d;
          &:before {
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            height: 100%;
            content: attr(data-text);
            transform: rotateX(-90deg);
            transform-origin: 50% 0;
            text-align: center;
          }
        }

        &:hover{

          .menu-text {
            transform: rotateX(90deg) translateY(-12px);
          }
        }
      }
    }
  }
}
//style working inline
